import { db, type LocalTask } from '@/lib/offline/db';
import { useAuth } from '@/components/layout/auth-provider';

export function useTaskClient() {
    const { farmId } = useAuth();

    const getTasks = async () => {
        if (!farmId) return [];
        return db.local_tasks.where('farmId').equals(farmId).toArray();
    };

    const createTask = async (task: Omit<LocalTask, 'id' | 'farmId' | 'updatedAt'>) => {
        if (!farmId) return;

        const id = `local_${Date.now()}`;
        const newTask: LocalTask = {
            ...task,
            id,
            farmId,
            updatedAt: new Date().toISOString(),
        };

        await db.local_tasks.add(newTask);

        // Queue job for sync
        await db.outbox_jobs.add({
            action: 'TASK_CREATE',
            data: newTask,
            priority: 1,
            attempts: 0,
            nextAttemptAt: Date.now(),
            farmId,
            status: 'PENDING',
        });

        return newTask;
    };

    const updateTask = async (id: string, updates: Partial<LocalTask>) => {
        if (!farmId) return;

        await db.local_tasks.update(id, {
            ...updates,
            updatedAt: new Date().toISOString(),
        });

        await db.outbox_jobs.add({
            action: 'TASK_UPDATE',
            data: { id, ...updates },
            priority: 1,
            attempts: 0,
            nextAttemptAt: Date.now(),
            farmId,
            status: 'PENDING',
        });
    };

    return { getTasks, createTask, updateTask };
}
