import Dexie, { type Table } from 'dexie';

export interface LocalEvent {
    id?: number;
    type: string;
    payload: any;
    timestamp: number;
    synced: 0 | 1;
    farmId: string;
}

export interface OutboxJob {
    id?: number;
    action: string;
    data: any;
    priority: number;
    attempts: number;
    nextAttemptAt: number;
    lastError?: string;
    farmId: string;
    status: 'PENDING' | 'FAILED' | 'PROCESSED';
}

export interface LocalTask {
    id: string; // Server ID
    farmId: string;
    title: string;
    description?: string;
    status: string;
    dueDate?: string;
    assigneeId?: string;
    templateId?: string;
    updatedAt: string;
}

export interface LocalMedia {
    id: string; // Internal blob ID or reference
    farmId: string;
    blob: Blob;
    type: string;
    metadata: any;
    uploaded: 0 | 1;
}

export interface SyncMeta {
    key: string; // e.g., 'tasks_cursor'
    farmId: string;
    value: string;
    updatedAt: number;
}

export class FarmDB extends Dexie {
    local_events!: Table<LocalEvent>;
    outbox_jobs!: Table<OutboxJob>;
    local_tasks!: Table<LocalTask>;
    local_media!: Table<LocalMedia>;
    sync_meta!: Table<SyncMeta>;

    constructor() {
        super('FarmDB');
        this.version(1).stores({
            local_events: '++id, type, farmId, synced, timestamp',
            outbox_jobs: '++id, action, farmId, status, nextAttemptAt',
            local_tasks: 'id, farmId, status, assigneeId, dueDate',
            local_media: 'id, farmId, uploaded',
            sync_meta: '[key+farmId]',
        });
    }
}

export const db = new FarmDB();
